.si-header {

  &__wrap {
    padding: var(--full-space) 0;
  }

  &__container {
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;

    &-lft {}

    &-rgt {}
  }

  &__logo {
    width: 160px;

    a {
      display: block;

      img {
        width: 100%;
        display: block;
      }
    }
  }
}